// Variables
$foreground: white
$background: black
$accent: #6a00ff

$gold: #c48508
$red: red
$green: green

\:root
  --foreground: #{$foreground}
  --foreground-75: #{rgba($foreground, .75)}
  --foreground-50: #{rgba($foreground, .50)}
  --foreground-25: #{rgba($foreground, .25)}

  --background: #{$background}
  --background-75: #{rgba($background, .75)}
  --background-50: #{rgba($background, .50)}
  --background-25: #{rgba($background, .25)}

  --accent: #{$accent}
  --accent-75: #{rgba($accent, .75)}
  --accent-50: #{rgba($accent, .50)}
  --accent-25: #{rgba($accent, .25)}
  --accent-10: #{rgba($accent, .10)}

  --gold: #{$gold}
  
  --red: #{$red}
  --red-75: #{rgba($red, .75)}
  --red-50: #{rgba($red, .50)}
  --red-25: #{rgba($red, .25)}
  --red-10: #{rgba($red, .10)}

  --green: #{$green}
  --green-75: #{rgba($green, .75)}
  --green-50: #{rgba($green, .50)}
  --green-25: #{rgba($green, .25)}
  --green-10: #{rgba($green, .10)}